#carousel{
    width: 1000px;
    height: 450px;
    border: solid 4px #fff;
    -webkit-box-shadow: 0 0 14px rgba(0,0,0,0.3);
            box-shadow: 0 0 14px rgba(0,0,0,0.3);
    position: relative;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    margin: auto;

}
.chevron{
    position: absolute;
    width: 70px;
    cursor: pointer;
    top: 200px;
} 
#gauche{
    left: 0;
    opacity: 0.6;
    -webkit-transition: all 0.5 ease;
    -o-transition: all 0.5 ease;
    transition: all 0.5 ease;
}
#droit{
    right: 0;
    opacity: 0.6;
    -webkit-transition: all 0.5 ease;
    -o-transition: all 0.5 ease;
    transition: all 0.5 ease;
}
#droit:hover{
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 1;
    -webkit-transition: all 0.5 ease;
    -o-transition: all 0.5 ease;
    transition: all 0.5 ease;
}
#gauche:hover{
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 1;
    -webkit-transition: all 0.5 ease;
    -o-transition: all 0.5 ease;
    transition: all 0.5 ease;
}
.image{
    width: 1000px;
    height: 450px;
    margin: 0;
    display: inline-block;
    background-size: cover;
    
}
.titreGalerie{
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    margin-top: 25px;
    margin-bottom: 50px;
}


